<template>
  <aside id="sidebar" class="column-left">

    <header>
      <img src="../assets/dhsp.png" width="200"/>
      <h2 style="color:white">Ngô Hùng Kiệt</h2>
      <h2 style="color:white">17.28.100301</h2>
      <h2 style="color:white">nhkiet12t8@gmail.com</h2>
    </header>

    <nav id="mainnav">
      <ul>
        <li v-on:click="setActive('home')" :class="{ 'selected-item': isActive('home') }"><a href="#/">Trang chủ</a></li>
        <li v-on:click="setActive('examples')" :class="{ 'selected-item': isActive('examples') }"><a href="#/examples">Giới thiệu</a></li>
        <li v-on:click="setActive('products')" :class="{ 'selected-item': isActive('products') }"><a href="#/products">Nhiệm vụ</a></li>
        <li v-on:click="setActive('solutions')" :class="{ 'selected-item': isActive('solutions') }"><a href="#/solutions">Thực hiện</a></li>
        <li v-on:click="setActive('contact')" :class="{ 'selected-item': isActive('contact') }"><a href="#/contact">Tham khảo</a></li>
        <li v-on:click="setActive('evaluation')" :class="{ 'selected-item': isActive('evaluation') }"><a href="#/evaluation">Đánh giá</a></li>
        <li v-on:click="setActive('conclusion')" :class="{ 'selected-item': isActive('conclusion') }"><a href="#/conclusion">Kết luận</a></li>
      </ul>
    </nav>
  </aside>
</template>

<script>
  export default {
    name: 'slider',
    data () {
      return { activeItem: 'home' }
    },
    methods: {
      isActive: function (menuItem) {
        return this.activeItem === menuItem
      },
      setActive: function (menuItem) {
        this.activeItem = menuItem // no need for Vue.set()
      }
    }
  }
</script>



